
.options-panel {
  margin-top: 12px;
  padding: 12px;
}

.head {
  margin-bottom: 10px;
}

.empty {
  color: var(--text-secondary);
  font-size: 14px;
}

.list {
  display: grid;
  gap: 8px;
}

.option-btn {
  border: 1px solid var(--border-color);
  background: #fff;
  border-radius: 8px;
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

.title {
  display: block;
  font-weight: 700;
}

.content {
  display: block;
  margin-top: 4px;
  color: var(--text-secondary);
  font-size: 14px;
}

.custom-row {
  margin-top: 10px;
  display: flex;
  gap: 8px;
}

.custom-input {
  flex: 1;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 8px;
}

.custom-btn {
  border: 1px solid var(--border-color);
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 8px 12px;
  cursor: pointer;
}
