
.top-nav {
  position: sticky;
  top: 0;
  z-index: 20;
  display: flex;
  gap: 8px;
  align-items: center;
  padding: 8px;
  margin-bottom: 12px;
}

.spacer {
  flex: 1;
}

.btn {
  border: 1px solid var(--border-color);
  background: var(--secondary-bg);
  color: var(--text-primary);
  border-radius: 8px;
  padding: 6px 10px;
  cursor: pointer;
}
