
.settings {
  padding: 12px;
  display: grid;
  gap: 12px;
}

.group {
  display: grid;
  gap: 6px;
}

select {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 8px;
  background: #fff;
}

.reset-btn {
  justify-self: start;
  border: 1px solid var(--border-color);
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 8px 12px;
  cursor: pointer;
}
