
.history {
  padding: 12px;
}

.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.btn {
  border: 1px solid var(--border-color);
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 6px 10px;
  cursor: pointer;
}

.search {
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 10px;
}

.empty {
  color: var(--text-secondary);
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 8px;
}

.item {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 8px;
}

.jump {
  border: none;
  background: transparent;
  color: var(--accent-color);
  cursor: pointer;
  font-weight: 700;
  padding: 0;
}

.preview {
  margin: 6px 0 0;
  color: var(--text-secondary);
  font-size: 14px;
  white-space: pre-wrap;
}
