
.start {
  margin-top: 12vh;
  padding: 24px;
  text-align: center;
}

.pov {
  margin: 14px auto 0;
  max-width: 520px;
  text-align: left;
  padding: 12px;
}

.pov h2 {
  margin: 0 0 8px;
  font-size: 16px;
}

.hint {
  margin: 0 0 8px;
  color: var(--text-secondary);
  font-size: 13px;
}

.done {
  margin-top: 14px;
}

.pov-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 0;
}

.start-btn {
  margin-top: 16px;
  border: 1px solid var(--border-color);
  background: var(--accent-color);
  color: #fff;
  border-radius: 8px;
  padding: 10px 18px;
  cursor: pointer;
}
